import "./main-bg-style.css";
import {motion} from "framer-motion";

export default function MainBackground(props) {
    return (
        <motion.div
            className={"mainBgBox"}
            initial={{x: "-50%", opacity: 0}}
            animate={{x: 0, opacity: 1}}
            transition={{duration: 2}}
        ><motion.span className={"unselectable"} initial={{rotate: 0}} animate={{rotate: -20}} transition={{duration: 2}}>XPLAGO</motion.span></motion.div>
    )
}