import "./acquaintance-style.css"
import MainImage from "./img/app_development_SVG.svg"
import ScrollBlock from "./scrollBlock";

import {motion} from "framer-motion";

export default function Acquaintance(props) {
    return (
        <section className={"acquaintanceBlock"}>
            <motion.div className={"textBox"} initial={{opacity: 0, x: "-100%"}} animate={{opacity: 1, x: 0, transition: {duration: 0.4}}}>
                <h2>Acquaintance</h2>
                <p>
                    Hi, my nickname is <strong>XPlaGo</strong>. I am a beginner web developer, I know HTML5, CSS5, JavaScript (ES6) along with React and Framer Motion.
                </p>
                <div>
                    <a className={"filledHref"} href={"#"}>MY WORKS</a>
                    <a className={"transparentHref"} href={"https://github.com/XPlaGo"}>MY GITHUB</a>
                </div>
            </motion.div>
            <motion.div className={"imageBox"} initial={{opacity: 0, x: "50%"}} animate={{opacity: 1, x: 0, transition: {delay: 0.5, duration: 1.5}}}>
                <img src={MainImage}/>
            </motion.div>
            {/*<ScrollBlock link={""}/>*/}
        </section>
    )
}