import "./menu-style.css";
import InstagramLogo from "./img/instagram.svg";
import GitHubLogo from "./img/github.svg";

import {AnimatePresence, AnimateSharedLayout, motion} from "framer-motion";
import {useState} from "react";

export default function Menu(props) {
    const [selectedItem, setSelectedItem] = useState(null)
    const NavMenuList = [{title: "Acquaintance"}, {title: "Later..."}]
    const NavMenu = NavMenuList.map((data) => {
        return <MenuNavItem data={data} key={data.title} selectedItem={selectedItem} setSelectedItem={setSelectedItem}/>
    })
    return (
        <AnimatePresence>
            {props.menuState &&
            <motion.div
                onMouseLeave={() => props.setMenuState(false)}
                //key={"menu"}
                initial={{x: "100%"}}
                exit={{x: "100%"}}
                animate={{x: 0}}
                transition={{type: "easeInOut", duration: 0.3}}
                className={"menuBlock"}>
                <div className={"menuHeader"}>
                    <div
                        onClick={() => {
                            props.setMenuState(false);
                            console.log("close");
                        }}
                        className={"menuCloseButton"}>
                        <span>{}</span>
                        <span>{}</span>
                    </div>
                </div>
                <AnimateSharedLayout>
                    <ul className={"menuNavUl"}
                        //onMouseLeave={() => setSelectedItem(null)}
                    >
                        <AnimatePresence>
                            {NavMenu}
                        </AnimatePresence>
                    </ul>
                </AnimateSharedLayout>
                <ul className={"menuSocialUl"}>
                    <li><a href={"https://www.instagram.com/x_pla_go"}><img alt={"In"} src={InstagramLogo}/></a></li>
                    <li><a href={"https://github.com/XPla-Go"}><img alt={"GH"} src={GitHubLogo}/></a></li>
                </ul>
            </motion.div>}
        </AnimatePresence>
    )
}

function MenuNavItem(props) {
    return (
        <li onMouseEnter={() => props.setSelectedItem(props.data.title)}>
                {props.selectedItem === props.data.title &&
                <motion.span
                    className={"menuNavItemSpan"}
                    layoutId={"span"}
                    initial={{opacity: 0}}
                    animate={{opacity: 1}}
                    transition={{duration: 0.2}}
                    //key={props.data.title}
                    >{}</motion.span>
                }
            {props.data.title}</li>
    )
}